.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 10px;
    color: white;
}

.dialogsItems .active {
    color: gold;
}

.messages {
    padding: 10px;
}

.messages .message {
    color: white;
}

.ebutton {
    min-width: 70px;
    font-family: inherit;
    appearance: none;
    border: 0;
    border-radius: 5px;
    background: #4676d7;
    color: #fff;
    padding: 4px 8px;
    font-size: 1rem;
    cursor: pointer;
    margin: 10px;
}

.ebutton:hover {
    background: #1d49aa;
}

.ebutton:focus {
    box-shadow: 0 0 0 4px #cbd6ee;
}